* {
  margin: 0;
  padding: 0;
}

#root {
    box-sizing: border-box;
    height: 93vh;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    outline: none;
    border-radius: 0;
}

.loading {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.header .row {
    display: flex;
    justify-content: space-between;
}
.goalform {
    width: 80vw;
    min-width: 320px;
    max-width: 960px;
    /* min-height: 600px; */
}

.goalform .row {
    display: flex;
}

.goalform .row.right {
    justify-content: flex-end;
}
.centeredModal {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    align-items: center; 
}
.timer-strike_animate {
    -webkit-animation: strike 2s ease 0s 1 forwards;
            animation: strike 2s ease 0s 1 forwards;
}
  
.timer-strike {
    /* text-decoration: line-through red; */
    color: red;
}
  
@-webkit-keyframes strike {
    100% {
        /* opacity: 0; */
        /* text-decoration: line-through red; */
        color: red;
    }
}
  
@keyframes strike {
    100% {
        /* opacity: 0; */
        /* text-decoration: line-through red; */
        color: red;
    }
}
.dash {
    position: relative;
    width: 100%;
    height: 100%;
}

#dash-addBtn {
    position: fixed;
    bottom: 40px;
    right: 40px;
    padding: 0;
}

#dash-addBtn:hover {
    padding: 0 16px;
}

#dash-addBtn:hover::after {
    content:"Add Goal";
    white-space:nowrap;
}
.App {
    height: 100%;
    width: 100%;
}
